						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="fahuoren">
							<el-input class="list_inp" v-model="form.fahuoren" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.fahuoren?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="fahuorenxingming">
							<el-input class="list_inp" v-model="form.fahuorenxingming" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.fahuorenxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="kehuzhanghao">
							<el-input class="list_inp" v-model="form.kehuzhanghao" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.kehuzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="kehuxingming">
							<el-input class="list_inp" v-model="form.kehuxingming" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.kehuxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="xiadanshijian">
							<el-input class="list_inp" v-model="form.xiadanshijian" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.xiadanshijian?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="dagaijuli">
							<el-input class="list_inp" v-model.number="form.dagaijuli" placeholder=""
								 type="number" 								:readonly="!isAdd||disabledForm.dagaijuli?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="huowuyunfei">
							<el-input class="list_inp" v-model.number="form.huowuyunfei" placeholder=""
								 type="number" 								:readonly="!isAdd||disabledForm.huowuyunfei?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="jiehuoshijian">
							<el-date-picker
								class="list_date"
								v-model="form.jiehuoshijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.jiehuoshijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="hetong">
							<uploads
								:disabled="!isAdd||disabledForm.hetong?true:false"
								type="file"
								action="file/upload" 
								tip="" 
								:limit="1" 
								style="width: 100%;text-align: left;"
								:fileUrls="form.hetong?form.hetong:''" 
								@change="hetongUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="peichezhuangtai">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.peichezhuangtai?true:false"
								v-model="form.peichezhuangtai" 
								placeholder=""
								>
								<el-option v-for="(item,index) in peichezhuangtaiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick"></el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'jiehuoxinxi'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
		piaojudanhao : false,
		huowumingcheng : false,
		huowuleixing : false,
		qishidian : false,
		mudedi : false,
		huowushuliang : false,
		huowuzhongliang : false,
		huowujiage : false,
		fahuoren : false,
		fahuorenxingming : false,
		kehuzhanghao : false,
		kehuxingming : false,
		xiadanshijian : false,
		dagaijuli : false,
		huowuyunfei : false,
		jiehuoshijian : false,
		hetong : false,
		peichezhuangtai : false,
		crossuserid : false,
		crossrefid : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		piaojudanhao: [
		],
		huowumingcheng: [
		],
		huowuleixing: [
		],
		qishidian: [
		],
		mudedi: [
		],
		huowushuliang: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		huowuzhongliang: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		huowujiage: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		fahuoren: [
		],
		fahuorenxingming: [
		],
		kehuzhanghao: [
		],
		kehuxingming: [
		],
		xiadanshijian: [
		],
		dagaijuli: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		huowuyunfei: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		jiehuoshijian: [
		],
		hetong: [
		],
		peichezhuangtai: [
		],
		crossuserid: [
		],
		crossrefid: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const hetongUploadSuccess=(e)=>{
		form.value.hetong = e
	}
	//
	const peichezhuangtaiLists = ref([])
	//methods

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			piaojudanhao: '',
			huowumingcheng: '',
			huowuleixing: '',
			qishidian: '',
			mudedi: '',
			huowushuliang: '',
			huowuzhongliang: '',
			huowujiage: '',
			fahuoren: '',
			fahuorenxingming: '',
			kehuzhanghao: '',
			kehuxingming: '',
			xiadanshijian: '',
			dagaijuli: '',
			huowuyunfei: '',
			jiehuoshijian: '',
			hetong: '',
			peichezhuangtai: '',
			crossuserid: '',
			crossrefid: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.jiehuoshijian = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'logistics'){
			isAdd.value = false
			formTitle.value = ''
			getInfo()
		}else if(formType == 'reply'){
			type.value = formType
			isAdd.value = true
			disabledForm.value.cpicture = true
			disabledForm.value.content = true
			formTitle.value = ''
			getInfo()
		}else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='piaojudanhao'){
					form.value.piaojudanhao = row[x];
					disabledForm.value.piaojudanhao = true;
					continue;
				}
				if(x=='huowumingcheng'){
					form.value.huowumingcheng = row[x];
					disabledForm.value.huowumingcheng = true;
					continue;
				}
				if(x=='huowuleixing'){
					form.value.huowuleixing = row[x];
					disabledForm.value.huowuleixing = true;
					continue;
				}
				if(x=='qishidian'){
					form.value.qishidian = row[x];
					disabledForm.value.qishidian = true;
					continue;
				}
				if(x=='mudedi'){
					form.value.mudedi = row[x];
					disabledForm.value.mudedi = true;
					continue;
				}
				if(x=='huowushuliang'){
					form.value.huowushuliang = row[x];
					disabledForm.value.huowushuliang = true;
					continue;
				}
				if(x=='huowuzhongliang'){
					form.value.huowuzhongliang = row[x];
					disabledForm.value.huowuzhongliang = true;
					continue;
				}
				if(x=='huowujiage'){
					form.value.huowujiage = row[x];
					disabledForm.value.huowujiage = true;
					continue;
				}
				if(x=='fahuoren'){
					form.value.fahuoren = row[x];
					disabledForm.value.fahuoren = true;
					continue;
				}
				if(x=='fahuorenxingming'){
					form.value.fahuorenxingming = row[x];
					disabledForm.value.fahuorenxingming = true;
					continue;
				}
				if(x=='kehuzhanghao'){
					form.value.kehuzhanghao = row[x];
					disabledForm.value.kehuzhanghao = true;
					continue;
				}
				if(x=='kehuxingming'){
					form.value.kehuxingming = row[x];
					disabledForm.value.kehuxingming = true;
					continue;
				}
				if(x=='xiadanshijian'){
					form.value.xiadanshijian = row[x];
					disabledForm.value.xiadanshijian = true;
					continue;
				}
				if(x=='dagaijuli'){
					form.value.dagaijuli = row[x];
					disabledForm.value.dagaijuli = true;
					continue;
				}
				if(x=='huowuyunfei'){
					form.value.huowuyunfei = row[x];
					disabledForm.value.huowuyunfei = true;
					continue;
				}
				if(x=='jiehuoshijian'){
					form.value.jiehuoshijian = row[x];
					disabledForm.value.jiehuoshijian = true;
					continue;
				}
				if(x=='hetong'){
					form.value.hetong = row[x];
					disabledForm.value.hetong = true;
					continue;
				}
				if(x=='peichezhuangtai'){
					form.value.peichezhuangtai = row[x];
					disabledForm.value.peichezhuangtai = true;
					continue;
				}
				if(x=='crossuserid'){
					form.value.crossuserid = row[x];
					disabledForm.value.crossuserid = true;
					continue;
				}
				if(x=='crossrefid'){
					form.value.crossrefid = row[x];
					disabledForm.value.crossrefid = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.peichezhuangtai=''
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
		})
		peichezhuangtaiLists.value = ",".split(',')
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save=()=>{
		if(form.value.hetong!=null) {
			form.value.hetong = form.value.hetong.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(``,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(``,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		border-radius: 6px;
		padding: 30px;
		// form item
		:deep(.el-form-item) {
			margin: 0 0 20px 0;
			display: flex;
			//label
			.el-form-item__label {
			 color: #9E9E9E;
			 display: block;
			 width: 90px;
			 text-align: center;
			}
			// 
			.el-form-item__content {
				display: flex;
				width: calc(100% - 90px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 
				.list_inp {
					border: 2px solid #5FB7FF;
					border-radius: 20px;
					padding: 0 10px;
					background: #fff;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					height: 36px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//
				.list_date {
					border: 2px solid #5FB7FF;
					border-radius: 20px;
					background: #fff;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 
				.list_sel {
					border: 2px solid #5FB7FF;
					border-radius: 20px;
					padding: 0 10px;
					background: #fff;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
								padding: 0;
							}
							.is-focus {
