<template>
	<div>
		<div class="register_view">
			<div class="outTitle_view">
				<div class="outTilte">{{projectName}}</div>
			</div>
			<el-form :model="registerForm" class="register_form">
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.kehuzhanghao" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.mima" 
						 placeholder=""
						 type="password"
						 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp" v-model="registerForm.mima2" type="password" placeholder="" />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.kehuxingming" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<div :style='{"width":"calc(100% - 120px)"}' class="list_file_list">
						<uploads
							action="file/upload" 
							tip="" 
							:limit="3"
							:fileUrls="registerForm.touxiang?registerForm.touxiang:''" 
							@change="touxiangUploadSuccess">
						</uploads>
					</div>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-select 
						class="list_sel"
						v-model="registerForm.xingbie" 
						placeholder=""
						>
						<el-option v-for="item in kehuxingbieLists" :label="item" :value="item"></el-option>
					</el-select>
				</div>

				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.shouji" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_btn">
					<el-button class="register" type="success" @click="handleRegister"></el-button>
					<div class="r-login" @click="close"></div>
				</div>
			</el-form>
		</div>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context?.$project.projectName
	//
	import { useRoute } from 'vue-router';
	const route = useRoute()
	const tableName = ref('kehu')
	
	const registerForm = ref({
        xingbie: '',
	})
	const kehuxingbieLists = ref([])
	const init=()=>{
		kehuxingbieLists.value = ",".split(',')
	}
    const touxiangUploadSuccess=(fileUrls)=> {
        registerForm.value.touxiang = fileUrls;
    }
	// 
	//
	const handleRegister = () => {
		let url = tableName.value +"/register";
		if((!registerForm.value.kehuzhanghao)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if((!registerForm.value.mima)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.mima!=registerForm.value.mima2){
			context?.$toolUtil.message('','error')
			return false
		}
		if((!registerForm.value.kehuxingming)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.touxiang!=null){
			registerForm.value.touxiang = registerForm.value.touxiang.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(registerForm.value.shouji&&(!context?.$toolUtil.isMobile(registerForm.value.shouji))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		
		context?.$http({
			url:url,
			method:'post',
			data:registerForm.value
		}).then(res=>{
			context?.$toolUtil.message('','success', obj=>{
				context?.$router.push({
					path: "/login"
				});
			})
		})
	}
	//
	const getUUID=()=> {
		return new Date().getTime();
	}
	//
	const close = () => {
		context?.$router.push({
			path: "/login"
		});
	}
	init()
</script>
<style lang="scss" scoped>
	
	.register_view {
		background-repeat: no-repeat;
		background-size: cover !important;
		background: url(http://clfile.zggen.cn/20230926/1ab7a156f6244b56a57bda993d989b2e.png);
		display: flex;
		min-height: 100vh;
		justify-content: center;
		align-items: center;
		position: relative;
		background-position: center center;
		// 
		.outTitle_view {
			margin: -25px 0 0;
			top: 50%;
			left: 0;
			width: 29%;
			line-height: 50px;
			position: fixed;
			text-align: center;
			height: 50px;
			.outTilte {
				color: #5EB6FE;
				font-weight: bold;
				letter-spacing: 6px;
				font-size: 48px;
			}
		}
		// 
		.register_form {
			border-radius: 10px;
			padding: 30px 20px;
			margin: 0 0 0 100px;
			background: transparent;
			display: flex;
			width: 750px;
			justify-content: center;
			flex-wrap: wrap;
		}
		// item
		.list_item {
			margin: 10px 0;
			display: flex;
			width: 100%;
			justify-content: flex-start;
			align-items: center;
			// label
			.list_label {
				color: #9E9E9E;
				width: 90px;
				font-size: 14px;
				box-sizing: border-box;
				text-align: center;
			}
			// 
			:deep(.list_inp) {
				border-radius: 20px;
				padding: 0 10px;
				background: #fff;
				width: calc(100% - 90px);
				border-color: #5FB7FF;
				outline-offset: 4px;
				border-width: 2px;
				line-height: 36px;
				box-sizing: border-box;
				border-style: solid;
				height: 36px;
				//
				.el-input__wrapper{
					border: none;
					box-shadow: none;
					background: none;
					border-radius: 0;
					height: 100%;
					padding: 0;
				}
				.is-focus {
					box-shadow: none !important;
				}
			}
		}
		//
		:deep(.list_sel) {
			border: 2px solid #5FB7FF;
			border-radius: 20px;
			padding: 0 10px;
			background: #fff;
			width: calc(100% - 90px);
			line-height: 36px;
			box-sizing: border-box;
			//
			.select-trigger{
				height: 100%;
				.el-input{
					height: 100%;
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
			}
		}
		//
		.list_btn {
			margin: 20px 0 0;
			display: flex;
			width: 100%;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
			//
			.register {
					border: none;
					border-radius: 0;
					color: #fff;
					background: linear-gradient(90deg, #FB843D 0%, #9EC8EA 0%, #5DB6FF 100%);
					width: 80%;
					font-size: 20px;
					height: 40px;
			}
			//
			.register:hover {
				border: none;
				border-radius: 0;
				background: linear-gradient(270deg, #FB843D 0%, #9EC8EA 0%, #5DB6FF 100%);
				width: 80%;
				font-size: 20px;
				height: 40px;
			}
			//
			.r-login {
				cursor: pointer;
				padding: 10px 0 0;
				color: #999;
				width: 100%;
				font-size: 12px;
				text-align: center;
			}
		}
		//
		.list_file_list  {
			//
			:deep(.el-upload__tip){
				margin: 7px 0 0;
				color: #999;
				display: none;
				font-size: 12px;
				justify-content: flex-start;
				align-items: center;
			}
			//
			:deep(.el-upload--picture-card){
				border: 2px solid #5FB7FF;
				cursor: pointer;
				background-color: #fff;
				border-radius: 20px;
				width: 90px;
				line-height: 100px;
				text-align: center;
				height: 90px;
				//
				.el-icon{
					color: #9E9E9E;
					font-size: 32px;
				}
			}
			:deep(.el-upload-list__item) {
				border: 2px solid #5FB7FF;
				cursor: pointer;
				background-color: #fff;
				border-radius: 20px;
				width: 90px;
				line-height: 100px;
				text-align: center;
				height: 90px;
			}
		}
	}
</style>